* {
  box-sizing: border-box;
}

body {
  background-color: #f0f0f0;
  font-family: Roboto, sans-serif;
}

.loader {
  text-align: center;
  color: #000;
  margin: 20% auto;
  font-size: 24px;
  font-weight: 400;
  position: relative;
}

.loader:before {
  content: "Please wait";
  z-index: 5;
  position: relative;
}

.loader:after {
  content: "";
  height: 6px;
  width: 100%;
  background: linear-gradient(#515ddd, rgba(0, 0, 0, 0)) 0 0 / 0%;
  border: 1px solid;
  border-radius: 10px;
  animation: loadingLine 10s linear infinite;
  position: absolute;
  bottom: 0;
}

@keyframes loadingLine {
  to {
    background-size: 100%;
  }
}

.breed-select {
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 10px;
  padding: 10px;
  font-size: 16px;
}

.error {
  color: #e74c3c;
  text-align: center;
  margin-top: 20px;
  font-weight: 700;
  display: none;
}

.cat-info {
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
  display: none;
}

.cat-info img {
  max-width: 100%;
  height: auto;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .1);
}

.title {
  margin: 10px 0;
  font-size: 28px;
  font-weight: 700;
}

.description {
  max-width: 100%;
  text-align: center;
  margin: 10px 0;
  font-size: 20px;
  font-weight: 500;
}

